import React from 'react';
import s from './Banner.module.css';

const Text = () => {
    return (
        <div className={s.text}>
            <h1>An AI learning to be an Artist</h1>
            <h2>
                Join the Family and help AiCasso become 
                <br/>a world renown Artist delivering 
                <br/>Ai services on demand.
            </h2>
            <h3>
                75% of proceeds from mint and royalties 
                <br/>will go to creating Community and 
                <br/>Development Fund managed by DAO for 
                <br/>perpetual yield generation for 
                <br/>holders and project development.
            </h3>
            <button>Mint</button>
            <img src="/img/Fin.png"/>

        </div>
    )
}

export default Text;