import React from 'react';
import s from './Nav.module.css';

const Menu = (props) => {
    let menu = props.list.map( list => ( <li>
                                            <a href={list.link}>
                                                {list.title}
                                            </a>
                                         </li> ) )
    return (
        <div className={s.menu}>
            <ul>
                {menu}
              
            </ul>
            <div className={s.nav}>
                <a href="https://facebook.com" target="_blank">
                   <img src="/img/facb.png" alt="Facebook"/>
                </a>
                <a href='https://instagram.com/aicassonft?utm_medium=copy_link' target="_blank">
                   <img src="/img/inst.png" alt="Instagram"/>
                </a>
                <a href='https://twitter.com/Ai_casso' target="_blank">
                   <img src="/img/Twit.png" alt="Twit"/>
                </a>
                <a href='https://discord.com/invite/PeDPYFGPAQ' target="_blank">
                   <img src="/img/Disc.png" alt="Disc"/>
                </a>
            </div>
        </div>
    )
}

export default Menu;